@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.boddy{
  background: #000000;
}
.navbar{
  display: flex;
  flex-direction: column;
  background: #191919;
  font-family: Inter;
  color: #ebebeb;
}
.navbar-top{
  display: flex;
  align-items: center;
  align-content: center;
}
.search-option{
  margin: 1rem 0;
  border-radius: 10px;
  border-color: white;
  border-width: 0.01rem;
  padding: 0.4rem 0.8rem;
  color: #8c8ca1;
  font-size: 16px;
  background: black;
  font-family: "Work Sans";
}
.title-in{
  /* text-align: center; */
  display: flex;
  font-size: 16px;
}
.line{
  margin: 1.3rem auto;
  width: 100%;
  border-bottom: 0.1rem solid #575767;
}
li{
  font-size: 14px;
  list-style: none;
}
.hide{
  display: none;
}
.search-option:focus{
  outline: 0;
  border-color: #0086ff;
  border-width: 0.1rem;
  /* box-shadow: 0 1px 4px rgba(255, 255, 255, 0.8); */
}
.toggled-region{
  transition: height 5s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
.navbar-image{
  position: absolute;
  left: 20px;
  top: 10px;
  height: 60px;
  width: auto;
  cursor: pointer;
}
.profile-card{
  display: none;
}
.power-attr{
  padding: 1rem;
  margin: 2rem;
  background: #191919;
  border: 2px solid #323232;
  box-sizing: border-box;
  border-radius: 24px;
}
.checkbox-lg .custom-control-label::before{
  content: "";
  background-color: black;
  border: none;
  border-radius: 20px;
  top: 0.05rem;
  left: -1.6rem;
  width: 1.35rem;
  height: 1.35rem;
}
.custom-control-input:focus
{
  border-radius: 10px;
  outline: 0;
}

.custom-control-label{
  padding-left: 5px;
  border-radius: 10px;
}

.custom-checkbox{
  padding: 0.1rem;
  border-radius: 10px;
}
.custom-checkbox
.custom-control-label::before {
  border-radius: 10px;
}
.checkbox-lg .custom-checkbox:checked
.custom-control-label::after{
  border-radius: 10px;
  position: absolute;
  top: 1rem;
  width: 1.35rem;
  height: 1.35rem;
  background-color: #0086FF;
}
.title-out{
  color: #8f8f90;
  font-family: Inter;
  font-size: 20px;
  text-align: center;
  font-weight: 600;
  margin: 1rem 0;
}
.vline{
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid gray;
}
.ccol{
  display: flex;
  margin: 0.3rem 1rem;
}
.custom-control-label{
  color: #CCCDCD;
  padding:0.3rem;
  font-size: 13px;
  font-weight: 600;
  font-family: Inter;
}
@media screen and (min-width:750px){
  .hide{
    display: inline;
  }
  .custom-control-label{
    padding: 0;
  }
  .each{
    margin: 0 auto;
  }
  .navbar-image{
    grid-area: hello;
    height: 100px;
    top: 20px;
    margin-left: 2.5rem;
    width: auto;
  }
  .title-out{
    margin-left: 2rem;
    text-align: left;
  }
  .navbar-top{
    position: absolute;
    left: 20px;
    display: inline;
  }
  .search-option{
    position: absolute;
    top: 130px;
    background: #000000;
    border: 1px solid #0086FF;
    box-shadow: 0px 0px 0px 4px rgba(0, 134, 255, 0.31);
    border-radius: 8px;
  }
  .navbar-top span{
    position: absolute !important;
    top: 155px !important;
    left: 215px;
  }
  .navbar{
    height: 100vh;
    width: 255px;
    border-right: 2px solid #323232;
  }
  .title-in{
    margin: 0.7rem 1rem;
  }
  li{
    margin: 0.20rem 0;
  }
  .toggled-region{
    position: absolute;
    left: 30px;
    top: 210px;
  }
  .profile-card{
    display: inline;
    margin-bottom: 1rem;
  }
  .profile-image{
    height: 60px;
    margin: 0 1.2rem 0 1rem;
    border-radius: 35px;
    border: 3px solid #0086ff;
  }
  .remaining{
    position: absolute;
    left: 280px;
    width: 80%;
    top: 20px;
  }
  .power-attr{
    display: flex;
    margin-top: 10px;
    align-content: space-around;
    align-items: center;
  }
  .vline{
    margin: 0 1rem;
    border-left: 1px solid gray;
    height: 100px;
  }
  .custom-control-label{
    color: #CCCDCD;
    padding-left: 4px;
    font-size: 12px;
    font-weight: 600;
    font-family: Inter;
  }
}
@media screen and (max-width:450px){
  .search-option{
    margin-left: 70px;
  }
  .ccol{
    display: flex;
    margin: 0rem 1rem;
    flex-direction: column;
  }
}
.card-container{
    width: 80vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-row-gap: 0.9rem;
    /* grid-column-gap: 2px; */
}
.card-container img{
    margin: 0.9rem;
    margin-left: 3rem;
}
.card-container h1{
    position: absolute;
    margin-right: 1rem;
    color: orange;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    font-size: 2rem;
}
.myCard{
    margin: 1rem auto;
    display: block;
    background: #191919;
    width: 300px;
    border: 2px solid #323232;
    box-sizing: border-box;
    box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.03), 0px 51.1542px 51.419px rgba(0, 0, 0, 0.0182814), 0px 31.2614px 32.7222px rgba(0, 0, 0, 0.0145485), 0px 19.7px 20.341px rgba(0, 0, 0, 0.0131802), 0px 12.1574px 11.8781px rgba(0, 0, 0, 0.012699), 0px 6.89112px 6.09643px rgba(0, 0, 0, 0.0121579), 0px 3.01109px 2.28593px rgba(0, 0, 0, 0.0102347);
    border-radius: 32px;
}
.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 5px;
    border-radius: 5px;  
    background: #dcdcdc;
    outline: none;
    opacity: 0.7;
    transition: opacity .2s;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: white;
    color: white;
    cursor: pointer;
  }
  
  .slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
  }
.activated{
    color: white !important;
    margin-left: 1.4rem !important;
    font-weight: 800;
    border-color: white;
    background: linear-gradient(180deg, #2B2D37 0%, #1D1E24 100%);
    border: 2px solid #575767;
    box-sizing: border-box;
    border-radius: 100px;
}
.btn{
    color: #9898A0;
    background: #000;
    border-radius: 100px;
    font-family: Inter;
    font-weight: 800;
    font-family: 15px;
    margin: 0rem 0.5rem 1rem 0.5rem;
    /* border-color: #9898a0; */
}
.superpower{
    padding: 0.25rem 1.5rem;
    color: white;
    font-family: Inter;
}
.superpower h5{
    color: #575767;
    font-size: 13px;
    text-transform: uppercase;
}
.powers-list{
    list-style: none;
    display: flex;
    font-size: 15px;
    word-spacing: 0.2rem;
    text-transform: capitalize;
    padding: 0.5rem 0 1rem 0;
    border-bottom: 1px solid #575767;
}
.card-header{
    display: flex;
    margin-left: -40px;
    align-content: center;
    align-items: center;
    color: #ebebeb;
}
.card-header img{
    height: 70px;
    width: 70px;
    border-radius: 50px;
    border: 3px solid;
    border-color: #0086ff;
    margin-right: 15px;
}

.card-header h3{
    margin-top: 0.5rem;
    font-size: 20px;
    color: #EBEBEB;
    text-shadow: 0px 3px 7px rgba(0, 0, 0, 0.15);
    font-family: Inter;
    font-style: normal;
    font-weight: 800;
}
@media(min-width:700px){
    .myCard{
        margin: 1rem 0;
    }
}
